.blog {
  padding-top: 73px;

  @include tablet {
    padding-top: 13px;
  }
}

.blog h2 {
  margin: 0;
  padding: 0;

  @include title;
  font-size: 34px;
  line-height: 130%;
  text-align: center;
  letter-spacing: 0.03em;
}

.blog__grid {
  margin-top: 60px;
  display: grid;
  // grid-auto-flow: column;
  grid-row-gap: 30px;
  grid-column-gap: 30px;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content min-content;
  grid-auto-flow: row;

  @include tablet {
    grid-auto-flow: row;
    grid-template-columns: 1fr;
    grid-template-rows: min-content;
    grid-row-gap: 50px;
  }

  @include mobile {
    margin-top: 50px;
    grid-row-gap: 40px;
    grid-template-rows: auto;
  }
}

.blog__article {
  display: flex;
  background-color: $light-beige;
  background-repeat: no-repeat;
  background-size: contain;



  &--blog-trends {
    display: grid;
    grid-template-columns: 1fr 285px;
    grid-template-rows: 330px min-content;
  }

  @include tablet {

    &--blog-trends {
      display: flex;

    }
  }

  @include mobile {
    flex-direction: column;

    // height: 512px;
    // padding-top: 256px;
    &--blog-power {
      flex-direction: column-reverse;
    }
  }
}

.blog__article-bg {
  width: 50%;
  height: 300px;
  background-repeat: no-repeat;
  background-size: cover;

  @include mobile {
    width: 100%;
    height: 256px;
  }

  &--blog-care {
    background-image: url("../img/content/jess-harper-sunday-blog-care-desktop.webp");
    background-size: cover;


    @include retina {
      background-image: url("../img/content/jess-harper-sunday-blog-care-desktop@2x.webp");
    }

    @include tablet {
      // background-size: 50% 300px;
      background-size: cover;
    }

    @include mobile {
      // width: 100%;
      background-size: cover;
      background-position: center;
    }

    @include mobile500 {
      // background-size: contain;
    }
  }

  &--blog-power {

    background-image: url("../img/content/alex-holyoake-unsplash-blog-power-desktop.webp");
    background-position-x: right;

    @include retina {
      background-image: url("../img/content/alex-holyoake-unsplash-blog-power-desktop@2x.webp");
    }

    @include tablet {
      // background-size: 50% 300px;
      background-position: center;
    }

    @include mobile {
      // background-size: 100% 256px;
      // background-size: contain;
      // background-position: top;
      background-size: cover;
    }

    @include mobile500 {
      // background-size: cover;
      // background-size: contain;
    }
  }

  &--blog-trends {
    background-image: url("../img/content/alve-odonnell-unsplash-blog-trends-desktop.webp");
    background-size: cover;
    width: 100%;
    height: 100%;
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 3;

    @include retina {
      background-image: url("../img/content/alve-odonnell-unsplash-blog-trends-desktop@2x.webp");
    }

    @include tablet {
      height: 300px;
      // background-size: 50%;
      background-image: url("../img/content/alve-odonnell-blog-trends-tablet.webp");

      @include retina {
        // background-size: 50%;
        background-image: url("../img/content/alve-odonnell-blog-trends-tablet.webp");
      }
    }

    @include mobile {
      // background-size: contain;
      background-size: cover;
      // background-position: center;
      background-image: url("../img/content/alve-odonnell-blog-trends-mobile.webp");

      @include retina {
        background-image: url("../img/content/alve-odonnell-blog-trends-mobile@2x.webp");
      }
    }

    @include mobile500 {
      // background-size: cover;
      background-size: contain;
    }
  }
}

.blog__article p {
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 130%;
  text-align: center;
  color: $grey;
}

