.blog__article:nth-child(3n) {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 3;

  @include tablet {
    grid-column: auto;
    grid-row: auto;
  }
}

.blog__article-description {
  padding-top: 68px;
  padding-bottom: 10px;
  width: 50%;
  // height: 300px;
  margin-left: auto;
  background-color: $light-beige;

  @include mobile {
    padding-top: 49px;
    width: 100%;
    // height: 256px;
    margin-left: 0;
    padding-bottom: 50px;
  }
}

.blog__article:nth-child(3n) .blog__article-description {
  // margin-top: 330px;
